import { mapGetters, mapMutations, mapActions } from 'vuex';

export default{
	name:'MessageBox',
	computed: {
		...mapGetters(['messageBox'])
	},
	methods: {
		open(){
			this.messageBox.visible = true;
		},
		close(){
			this.messageBox.visible = false;
		},
		_success(){
			// this.success();
			this.messageBox.success()
			this.close();
		},
		_cancel(){
			// this.cancel(); 
			this.messageBox.cancel()
			this.close();
		}
	},
	mounted(){
		// this.open();
	}
}
